import React, {FC} from 'react';

import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, {SelectChangeEvent} from '@mui/material/Select';
import LanguageIcon from "../LanguageIcon/LanguageIcon";
import {LanguageEnum} from "../../enums/languageEnum";
import styles from './LanguageSelector.module.css'
import {useAppDispatch, useAppSelector} from "../../redux/store";
import {setLanguage} from "../../redux/Slices/lenguageSlice";

const LanguageSelector: FC = () => {
    const dispatch = useAppDispatch()
    const {language} = useAppSelector((state) => state.Language)
    const handleChange = (event: SelectChangeEvent) => {
        dispatch(setLanguage(event.target.value as LanguageEnum));
        localStorage.setItem("language", JSON.stringify(event.target.value as LanguageEnum));
    };
    return (
        <Box sx={{minWidth: 30}}>
            <FormControl fullWidth>
                <InputLabel id="label" className={styles.languageIconWrapper}><LanguageIcon/></InputLabel>
                <Select className={styles.languageSelector}

                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={language}
                        label="Language"
                        onChange={handleChange}
                >
                    <MenuItem value={LanguageEnum.US}>US</MenuItem>
                    <MenuItem value={LanguageEnum.UA}>UA</MenuItem>

                </Select>
            </FormControl>
        </Box>
    );
};

export default LanguageSelector;